body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header-color {
  background: #03001e; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #fdeff9,
    #ec38bc,
    #7303c0,
    #03001e
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.landing-grid {
  background: #03001e; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #fdeff9,
    #ec38bc,
    #7303c0,
    #03001e
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  text-align: center;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 100%;
}

.avatar-img {
  height: 250px;
  padding-top: 5em;
}

.banner-text {
  background-color: black;
  opacity: 0.6;
  width: 75%;
  margin: auto;
  border-radius: 10px;
}

.banner-text h1 {
  font-size: 55px;
  font-weight: bold;
  color: white;
  padding-top: 10px;
  margin-bottom: 0;
}

.banner-text hr {
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}

.banner-text p {
  color: white;
  font-size: 20px;
  padding: 1em;
}

.social-links {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.social-links i {
  color: white;
  font-size: 3em;
  padding: 8px;
}

.projects-grid {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.project-card {
  flex-shrink: 0;
  max-width: 330px;
  width: 100%;
  margin: 16px 8px;
}

.contact-body {
  margin: auto;
  position: absolute;
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top: 2em;
}

.contact-grid {
  justify-content: center;
  text-align: center;
  width: 80%;
  height: 550px;
  background: white;
  color: black;
  margin-bottom: 20px;
}

.contact-grid h2 {
  font-family: "Anton";
}

.contact-grid p {
  font-family: "Oxygen", sans-serif;
  font-weight: bold;
}

.contact-grid hr {
  border-top: 5px dotted black;
  width: 50%;
  margin: auto;
}

.contact-list i {
  font-size: 30px;
  padding-right: 2rem;
  width: 30px;
}

.contact-item {
  font-size: 20px;
  font-family: Anton;
}

/* Resume Page CSS */
.resume-right-col {
  background: #27221f;
  color: white;
  padding: 2em;
}

/* Contact Page CSS */
.contact-left {
  font-size: 50px;
  background: #b10dc9;
}

@media (max-width: 720px) {
  .banner-text h1 {
    font-size: 32px;
  }

  .contact-list i {
    font-size: 25px;
    padding-right: 1rem;
  }

  .contact-item {
    font-size: 13px;
  }
}

